import React from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import ContentPage from './views/ContentPage/ContentPage';
import ComingSoon from './views/ComingSoon';
import LandingPageV2 from './views/LandingPageV2/LandingPageV2';

function App() {
  return (
    <>
      <BrowserRouter>
        <Routes>
          <Route >
            <Route index element={<LandingPageV2 />} />
            <Route path="/:heading" element={<ContentPage />} />
            <Route path="/:heading/:subHeading" element={<ContentPage />} />
            <Route path="/:heading/coming-soon" element={<ComingSoon />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </>
  );
}

export default App;
